.header {
    width: 100%;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: transparent;
    padding: 0 10%;
  }
  
  .logo {
    font-size: 2rem;
    color: #258170;
    font-weight: 500;
    letter-spacing: -.1rem;
  }
  
  .header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
  }
  
  .header li {
    margin-left: 3rem;
  }
  
  .header a {
    text-decoration: none;
    font-size: 1.1rem;
    color: #000;
  }
  
  .header a:hover,
  .header a:active,
  .header a.active {
    color: #258170;
  }
  
  .badge {
    background-color: #258170;
    color: white;
    border-radius: 12px;
    padding: 0 1rem;
    margin-left: 0.5rem;
  }

  .badge:hover {
    background-color: #1d574d;
  }