.mv_area {
    /* display: flex;
    justify-content: center;
    align-items: center; */
    position: relative;
    top: -5rem;
    height: 65vh;
    margin: 0;
    z-index: -100;
    background: url(https://images.unsplash.com/photo-1578367622663-5b44691c3493?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1774&q=80);
    background-size: cover;
}

.text_area {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    position: relative;
    top: 45%;
    right: 10%;
}

.text_area > p {
    width: 400px;
    margin: 0 0 1rem 0;
    font-size: 1.5rem;
    font-weight: 700;
    color: #226d5f;
    text-align: right;
}

.button {
    width: 20vw;
    font: inherit;
    cursor: pointer;
    color: #258170;
    border: 1px solid #258170;
    background-color: transparent;
    padding: 0.5rem 1.5rem;
    border-radius: 4px;
    text-align: center;
  }

 .button > a {
    text-decoration: none;
    color: #258170;
 }
  
  .button:hover,
  .button:active {
    background-color: #258170;
    border: 1px solid transparent;
    color: #fff;
  }

  .custom-shape-divider-bottom-1660667604 {
    position: relative;
    top: 40%;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}

.custom-shape-divider-bottom-1660667604 svg {
    position: relative;
    display: block;
    width: calc(263% + 1.3px);
    height: 300px;
}

.custom-shape-divider-bottom-1660667604 .shape-fill {
    fill: #E9FCF9;
}