@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

* {
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  height: auto;
  background-color: #E9FCF9;
  /* background: url(https://images.unsplash.com/photo-1519162808019-7de1683fa2ad?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1375&q=80); */
}

h1 {
  font-size: 2rem;
  color: #2c292b;
}

h1 > span {
  font-size: 1rem;
  font-weight: 400;
  margin-left: 1rem;
}

h2 {
  font-size: 1.4rem;
  color: #2c292b;
}
